.presentatione__video {
  display: flex;
  flex-direction: column;
  text-align: center;
  font-family: @PTnormal;
}

.presentatione__video-heading {
  font-size: 18px;
  font-weight: bold;
  line-height: 1.44;
  text-transform: uppercase;
  margin-top: 38px;
}

.presentatione__video-description {
  padding-left: 41px;
  padding-right: 39px;
  font-size: 14px;
  line-height: 1.71;
  text-transform: uppercase;
}

.presentation__video-image {
  margin-top: 17px;
  width: 100%;
  height: auto;
}

@media (min-width: 768px) {
  .presentatione__video {
    margin-top: 28px;
    padding-left: 7.8%;
    padding-right: 7.8%;
    margin-bottom: 46px;
  }

  .presentatione__video-heading {
    font-size: 30px;
    font-weight: bold;
    line-height: 0.87;
  }

  .presentatione__video-description {
    font-size: 14px;
    line-height: 1.71;
    margin-top: 0;
  }

  .presentation__video-image {
    margin-top: 60px;
    height: 500px;
  }

  .presentatione__video-timer {
    width: 100%;
    height: 93px;
    left: 0;
  }

  .presentatione__video-timer-line {
    width: 60%;
    height: 3px;
    left: 14%;
  }

  .presentatione__video-timer-scroll {
    left: 31%;
    top: 43%;
  }

  .presentation__video-control {
    position: absolute;
    bottom: 0;
    z-index: 2;
    height: 93px;
    background-color: rgba(0, 0, 0, 0);
  }

  .presentation__video-button-captions {
    top: 41%;
    right: 23%;
  }

  .presentation__video-button {
    fill: black;
  }

  .presentation__video-button-replay {
    left: -2%;
    top: 40%;
  }

  .presentation__video-button-fullscreen {
    top: 41%;
    right: 15%;
  }

  .presentation__video-button-image {
    fill: white;
  }
}

@media (min-width: 1200px) {
  .presentatione__video {
    margin-top: 35px;
    margin-bottom: 88px;
  }

  .presentatione__video-description {
    padding-left: 290px;
    padding-right: 290px;
  }

  .presentatione__video-block {
    width: 800px;
    margin-left: auto;
    margin-right: auto;
  }

  .presentation__video-control {
    width: 800px;
    margin-left: 174px;
  }

  .presentation__video-button-captions {
    top: 37%;
    right: 19%;
  }

  .presentatione__video-timer-line {
    top: 47%;
    left: 11.5%;
  }

  .presentatione__video-timer-scroll {
    left: 25.5%;
    top: 41%;
  }

  .presentation__video-button-replay {
    left: -5%;
    top: 36%;
  }

  .presentation__video-button-fullscreen {
    top: 37%;
    right: 13%;
  }
}
